<template>
  <div class="ofertas">
    <NavTienda />
    <h3 class="display-4">Pelicula</h3>
    <b-row class="tabla_ofertas">
      <b-col
        cols="4"
        class="cajon_ofertas"
        v-for="(oferta) in ofertasPeliculas"
        :key="oferta.id"
      >
        <OfertaPelicula :oferta="oferta" />
      </b-col>
    </b-row>
    <h3 class="display-4">Oferta Especial</h3>
    <b-row class="tabla_ofertas">
      <b-col
        cols="6"
        class="cajon_ofertas_especial"
        v-for="(oferta) in ofertasEspecial"
        :key="oferta.id"
      >
        <OfertaEspecial />
      </b-col>
    </b-row>
    <h3 class="display-4">Series</h3>
    <b-row class="tabla_ofertas">
      <b-col
        cols="6"
        class="cajon_ofertas-serie"
        v-for="(oferta) in ofertasSeries"
        :key="oferta.id"
      >
        <OfertaSerie />
      </b-col>
    </b-row>
    <h3 class="display-4">Streaming</h3>
    <b-row class="tabla_ofertas">
      <b-col
        cols="4"
        class="cajon_suscripciones"
        v-for="(oferta) in ofertasStreaming"
        :key="oferta.id"
      >
        <OfertaSuscripcion />
      </b-col>
    </b-row>
  </div>
</template>

<script>
import OfertaPelicula from "@/components/OfertasTienda/OfertaPelicula.vue";
import NavTienda from "@/components/OfertasTienda/NavTienda.vue";
import OfertaEspecial from "@/components/OfertasTienda/OfertaEspecial.vue";
import OfertaSerie from "@/components/OfertasTienda/OfertaSerie.vue";
import OfertaSuscripcion from "@/components/OfertasTienda/OfertaSuscripcion.vue";
import { mapMutations, mapActions, mapGetters } from "vuex";

export default {
  name: "Tienda",
  components: {
    OfertaPelicula,
    OfertaEspecial,
    OfertaSerie,
    OfertaSuscripcion,
    NavTienda
  },
  computed: {
    ...mapGetters("ofertas", [
      "ofertasPeliculas",
      "ofertasEspecial",
      "ofertasSeries",
      "ofertasStreaming"
    ])
  },
  methods: {
    ...mapMutations(["addBreadcrumb"]),
    ...mapActions("ofertas", ["loadOfertasTipo"])
  },
  async created() {
    for (let i = 0; i <= 3; i++) {
      await this.loadOfertasTipo(i);
    }
    this.addBreadcrumb([
      {
        text: "Home",
        to: { name: "Home" }
      },
      {
        text: "Tienda",
        to: { name: "Tienda" }
      }
    ]);
  }
};
</script>

<style lang="scss" scoped>
.ofertas {
  text-align: center;
}
.display-4 {
  margin-top: 1rem;
  background-color: #f8f9fa;
}
.tabla_ofertas {
  margin: auto;
  padding-top: 50px;
}

.cajon_ofertas {
  float: left;
  margin-top: 20px;
  border: 1px solid #85c34a;
  background: #30353c;
}

.cajon_ofertas_especial {
  float: left;
  margin-top: 20px;
  background-color: #fff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.9);
  z-index: 500;
}

.cajon_ofertas-serie {
  float: left;
  margin-top: 20px;
  border: 1px solid #ffc107;
  background: #3c3030;
}

.cajon_suscripciones {
  float: left;
  margin-top: 20px;
  border: 1px solid #30353c;
  background: #291135;
  box-shadow: 0 0 8px rgba(141, 90, 23, 0.9);
}
</style>