<template>
  <div>
    <b-modal
      id="gestionOferta"
      title="Complete los datos"
    >
      <form @submit.prevent>
        <b-form-group
          id="input-group-tipo"
          label="Tipo de Oferta:"
          label-for="tipo"
        >
          <b-form-select
            id="tipo"
            v-model="tipo"
            :options="tipos"
            required
          ></b-form-select>
        </b-form-group>
        <b-form-group
          id="input-group-tipo"
          label="Titulo:"
          label-for="tipo"
        >
          <b-form-input
            v-model="titulo"
            type="text"
            placeholder="Ingrese titulo"
            :state="titulo.length ? true : false "
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="input-group-tipo"
          label="Precio:"
          label-for="tipo"
        >
          <b-form-input
            v-model="precio"
            type="number"
            placeholder="Ingrese precio"
            :state="precio.length ? true : false "
          ></b-form-input>
        </b-form-group>
        <b-form-group label="Items:">
          <b-input-group
            v-for="(item, index) in items"
            :key="index"
          >
            <b-form-input
              v-model="items[index].item"
              type="text"
              placeholder="Ingrese titulo"
              :state="items[index].item.length ? true : false "
            ></b-form-input>
            <b-input-group-append>
              <b-button
                variant="outline-danger"
                @click="eliminarItemOferta(index)"
              ><i
                  class="fa fa-times"
                  aria-hidden="true"
                ></i></b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
        <b-button
          variant="success"
          block
          @click="agregarItemOferta()"
        >
          Agregar Item
        </b-button>
      </form>
      <template v-slot:modal-footer="{ ok, cancel }">
        <b-button
          size="sm"
          variant="success"
          @click="
            ok();
            agregarOferta();
          "
        >
          Agregar
        </b-button>
        <b-button
          size="sm"
          variant="danger"
          @click="cancel()"
        >
          Cancelar
        </b-button>
      </template>
    </b-modal>
    <b-navbar
      toggleable="lg"
      type="light"
      variant="light"
    >
      <b-navbar-brand
        tag="h1"
        class="mb-0"
      ><i
          class="fa fa-shopping-cart"
          aria-hidden="true"
        ></i></b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-navbar-nav class="ml-auto">
        <b-button
          variant="primary"
          v-b-modal.gestionOferta
        >
          <i
            class="fa fa-plus-circle"
            aria-hidden="true"
          ></i> Agregar Oferta
        </b-button>
      </b-navbar-nav>
    </b-navbar>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import notificacion from "@/mixin/notificacion";
export default {
  name: "NavTienda",
  mixins: [notificacion],
  data() {
    return {
      precio: "",
      titulo: "",
      tipo: null,
      tipos: [
        { value: null, text: "Por favor seleccione un tipo de oferta" },
        { value: "0", text: "Peliculas" },
        { value: "1", text: "Especial" },
        { value: "2", text: "Serie" },
        { value: "3", text: "Streaming" }
      ],
      items: []
    };
  },
  computed: {
    getOferta() {
      return {
        precio: this.precio,
        titulo: this.titulo,
        tipo: this.tipo
      };
    }
  },
  methods: {
    ...mapActions("itemOfertas", ["insertItemOferta"]),
    ...mapActions("ofertas", ["insertOferta", "loadOfertasTipo"]),
    async agregarOferta() {
      const me = this;
      const resultOferta = await this.insertOferta(this.getOferta);
      this.show(resultOferta, "Oferta", "agregada");
      if (!resultOferta.error) {
        this.items.forEach(async item => {
          const resultItem = await me.insertItemOferta({
            item: item.item,
            id_oferta: resultOferta.id
          });
          this.show(resultItem, "item", "agregado");
        });
        this.precio = "";
        this.titulo = "";
        this.tipo = null;
        this.items = [];
        for (let i = 0; i <= 3; i++) {
          await this.loadOfertasTipo(i);
        }
      }
    },
    agregarItemOferta() {
      this.items.push({ item: "" });
    },
    eliminarItemOferta(index) {
      this.items.splice(index, 1);
    }
  }
};
</script>

<style lang="scss" scoped>
</style>